<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
    .titulo-principal.color-acento-contenido
      .titulo-principal__numero
        span 2
      h1 Produccin
    .row.justify-content-center
      .col-sm-9
        p La produccin puede definirse como el proceso mediante el cual una empresa transforma una serie de insumos en un producto terminado. Como se ha mencionado previamente, el objetivo de una organizacin es la generacin de valor a travs de la satisfaccin de necesidades identificadas en el mercado objetivo, esto implica la oferta de bienes o servicios que otros agentes no estn aportando a la sociedad, o no lo hacen de forma suficiente.
        .cajon.p-3.mb-5(style="background-color:#FFFAD0")(data-aos="flip-up")
          p La mecnica organizacional puede reducirse a un flujo compuesto por tres grandes momentos: en primer lugar, la obtencin de recursos materiales o tcnicos del ambiente, para ello puede valerse de operaciones mercantiles como la compra de materias primas, obtencin de crditos financieros, reclutamiento de personal entre otros, posteriormente la etapa de produccin que involucra tomar estos recursos y dotarlos de un propsito, bien sea transformndolos en un producto tangible o diseando un servicio concreto, y finalmente, la entrega de estos al mercado en forma de venta a los clientes.
      .col-sm-3
        figure(data-aos="flip-up")
          img(src='@/assets/curso/temas/tema2/1.svg', alt='Texto que describa la imagen')

    separador
    .titulo-segundo.color-secundario
      h2#t_2_1 2.1 Etapas

    p.mb-5(data-aos="fade-left") El proceso productivo pasa por varias fases con funciones especficas hasta dar con la venta y entrega del producto final, el desglose de la produccin puede presentarse as:

    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--gris")(data-aos="flip-up")
      .row.justify-content-center.align-items-center(titulo="Etapa analtica o de acopio")
        .col-sm-2.mb-3
          figure
            img(src='@/assets/curso/temas/tema2/2.svg', alt='Texto que describa la imagen')
        .col-md-9.mb-4.mb-md-0
          p En esta fase se da inicio al ciclo productivo, y como su nombre lo indica, se caracteriza porque la organizacin busca conseguir la materia prima necesaria para cumplir con las metas de produccin incurriendo en la menor cantidad de costos. La bsqueda de proveedores se centra en conseguir la mejor tarifa sin sacrificar calidad o cumplimiento. 

      div(titulo="Etapa de sntesis o produccin")
        .row.justify-content-center.align-items-center(titulo="Etapa analtica o de acopio")
          .col-sm-2.mb-3
            figure
              img(src='@/assets/curso/temas/tema2/3.svg', alt='Texto que describa la imagen')
          .col-md-9.mb-4.mb-md-0
            p Inicia el proceso de transformacin de las materias primas en un nuevo producto, se involucran elementos del costo como la mano de obra y otros costos indirectos de fabricacin. De manera paralela se establecen los procedimientos de supervisin y control, que buscan garantizar la calidad de los bienes producidos o servicios prestados, y aportar informacin utilidad para proponer las acciones de mejora a futuro.

      div(titulo="Etapa de acondicionamiento o procesamiento")
        .row.justify-content-center.align-items-center(titulo="Etapa analtica o de acopio")
          .col-sm-2.mb-3
            figure
              img(src='@/assets/curso/temas/tema2/4.svg', alt='Texto que describa la imagen')
          .col-md-9.mb-4.mb-md-0
            p Finalmente, el producto recibe las modificaciones y adecuaciones necesarias para satisfacer la demanda de los clientes. Esto incluye costos de embalaje o presentacin, almacenamiento, transporte y cualquier otro gasto asociado con  la entrega de los bienes o servicios.

    p(data-aos="fade-left").mt-5.mb-5 Para ilustrar la informacin anterior, se presenta la siguiente figura sobre el proceso de produccin de la leche en una granja pecuaria:

    .titulo-sexto.color-acento-contenido.offset-1
      h5 Figura 4
      span #[em Etapas en la produccin de leche]

    .row.justify-content-center
      .col-lg-10.desktop
        figure(data-aos="flip-up")
          img(src='@/assets/curso/temas/tema2/5.svg', alt='En la figura se observa las etapas de acopio, produccin y acondicionamiento.')

    .row.justify-content-center
      .col-lg-10.movil
        figure(data-aos="flip-up")
          img(src='@/assets/curso/temas/tema2/6.svg', alt='En la figura se observa las etapas de acopio, produccin y acondicionamiento.')


    separador
    .titulo-segundo.color-secundario
      h2#t_2_2 2.2 Producto

    p(data-aos="fade-left") Un producto es el resultado del ciclo productivo previamente explicado que busca satisfacer una o varias necesidades identificadas. Funciona como un eje central del objeto social de una compaa, puesto que es el medio para obtener una rentabilidad, generando as, un mayor valor en el tiempo.
    p(data-aos="fade-left") Los productos son puestos a disposicin de la demanda en un mercado determinado, los potenciales compradores tendrn la posibilidad de elegir entre este y las dems alternativas ofertadas por la competencia y votarn en funcin de sus preferencias o condiciones personales. Hacer atractivo un producto requiere contemplar una gran cantidad de variables como lo son, su calidad, el beneficio que ofrecen respecto a su precio, su facilidad de uso, entre otros.

    h3.titulo-tercero 2.2.1.	Clasificacin

    p.mb-5(data-aos="fade-left") Existen diversos criterios y sistemas para clasificar los productos, sin embargo, se propone la siguiente distribucin en funcin de tres de las caractersticas de ms fcil identificacin en cualquier tipo de bien o servicio:

    ImagenInfografica.color-secundario.mb-5.desktop
      template(v-slot:imagen)
        figure(data-aos="flip-up")
          img(src='@/assets/curso/temas/tema2/7.svg', alt='Texto que describa la imagen')

      .tarjeta.color-acento-botones.p-3(x="50.2%" y="43%" numero="")
        .h5.mb-2 Productos de negocios
        p Son bienes destinados a continuar en procesos productivos o reventa, esto implica que son comprados por productores o distribuidores antes de llegar al consumidor final. Se clasifican en 7 categoras.

      .tarjeta.color-acento-botones.p-3(x="29%" y="16.7%" numero="")
        .h5.mb-2 Equipo Mayor
        p Comprende bienes de gran escala fsica, como maquinaria pesada, edificios o generadores elctricos. Tienen una incidencia directa sobre la operacin de los negocios dado su alto costo y utilidad.
        
      .tarjeta.color-acento-botones.p-3(x="70.6%" y="16.8%" numero="")
        .h5.mb-2 Equipo accesorio
        p Incluye bienes de menor tamao como las herramientas o equipos porttiles, no hacen parte de los productos terminados pero aportan en su produccin. Ejemplo de estos son el mobiliario, las herramientas o los computadores. Su periodo de vida suele ubicarse en un intermedio entre los inventarios y el equipo mayor.

      .tarjeta.color-acento-botones.p-3(x="29.3%" y="25.7%" numero="")
        .h5.mb-2 Materias primas
        p Son los insumos naturales utilizados de forma directa para ser transformados en un nuevo producto. Por ejemplo, la leche para la produccin de queso, o la carne para la elaboracin de embutidos.

      .tarjeta.color-acento-botones.p-3(x="70.7%" y="25.8%" numero="")
        .h5.mb-2 Componentes
        p Corresponden a los bienes terminados y listos para ensamblarse en la produccin de un producto de mayores dimensiones. Por ejemplo, los chips para una computadora o las bujas y llantas para un automvil.

      .tarjeta.color-acento-botones.p-3(x="17.3%" y="77.8%" numero="")
        .h5.mb-2 Productos de consumo
        p Se destinan al uso personal, por lo que no se espera sean utilizados en procesos productivos posteriores de tipo comercial, se subdividen en categoras.

      .tarjeta.color-acento-botones.p-3(x="4.8%" y="31.6%" numero="")
        .h5.mb-2 De conveniencia
        p Se caracterizan por tener precios econmicos, por lo que adquirirlos no suele representar un gran esfuerzo para el comprador. Ejemplo de este tipo de productos son las golosinas, frutas, leche, hojas de papel o lapiceros.

      .tarjeta.color-acento-botones.p-3(x="4.8%" y="41%" numero="")
        .h5.mb-2 De comparacin
        p Son los productos que son sujetos a comparacin con otros similares, antes de ser comprados por el cliente. Usualmente se evala la calidad, precio, diseo, marca, por lo que puede implicar que se visiten varios establecimientos antes de la compra. En este rubro pueden incluirse bienes como vehculos, computadoras o celulares.

      .tarjeta.color-acento-botones.p-3(x="4.8%" y="50.4%" numero="")
        .h5.mb-2 De especialidad
        p Son todos los productos que tienen el mercado objetivo muy estrecho y su obtencin obedece a casos muy especiales en los que pocas empresas se encuentran, es normal encontrar esta categora en empresas tecnolgicas con casos como procesadores o repuestos de mquinas especiales para ciertos procesos.

      .tarjeta.color-acento-botones.p-3(x="4.8%" y="59.8%" numero="")
        .h5.mb-2 No buscados
        p Estos bienes suelen ser desconocidos por el comprador, o bien, aun conocindolos, no son de su inters. Usualmente son productos innovadores que no se han masificado en un mercado concreto, como aspiradoras robticas o detectores de humo, o productos de baja demanda como enciclopedias o seguros de vida.

      .tarjeta.color-acento-botones.p-3(x="29.3%" y="36%" numero="")
        .h5.mb-2 Materiales procesados
        p Tambin son utilizados para ser transformados en un producto, pero a diferencia de la materia prima, han sido objeto de procesamiento antes de incorporarse al nuevo ciclo productivo. Como ejemplo de estos, se puede hacer mencin de las lminas de acero o de los componentes qumicos, as como la harina o el azcar utilizados para producir pan.

      .tarjeta.color-acento-botones.p-3(x="70.7%" y="35.8%" numero="")
        .h5.mb-2 Suministros de operacin
        p Son utilizados en la operacin y produccin de los bienes, pero no se incorporan al mismo; usualmente son de bajo costo y tiene una duracin corta. Ejemplo de estos son consumibles como la gasolina, lubricantes, pintura o tornillos.

      .tarjeta.color-acento-botones.p-3(x="39.2%" y="7.1%" numero="")
        .h5.mb-2 Servicios de negocios
        p Incluye aquellos servicios necesarios y derivados de la operacin, su naturaleza es intangible pero su aporte es tan necesario como el de los dems productos. En esta categora se incluyen servicios de mantenimiento y reparacin, asesoras financieras o de publicidad.

      .tarjeta.color-acento-botones.p-3(x="83.1%" y="79.2%" numero="")
        .h5.mb-2 Productos segn su duracin y tangibilidad
        p El criterio para clasificar los productos es la cantidad de usos que se espera obtener de ellos, el tiempo que durar siendo til y si es tangible o intangible.

      .tarjeta.color-acento-botones.p-3(x="94.5%" y="31.6%" numero="")
        .h5.mb-2 No duraderos
        p Son aquellos bienes tangibles de un solo uso o de consumo rpido. Por ejemplo, alimentos como las bebidas gaseosas o las frituras, productos de aseo como desodorantes o bienes no reutilizables como el papel o la cartulina.

      .tarjeta.color-acento-botones.p-3(x="94.5%" y="41%" numero="")
        .h5.mb-2 Duraderos
        p En este rubro se incluyen bienes que pueden ser reutilizados un nmero significativo de veces. Por ejemplo, los electrodomsticos, las herramientas de ferretera o las prendas de vestir.

      .tarjeta.color-acento-botones.p-3(x="94.5%" y="50.4%" numero="")
        .h5.mb-2 Servicios
        p A diferencia de las dos subcategoras anteriores, estos productos son intangibles, es decir, carecen de materia, por lo que no pueden tocarse. Debido a la dificultad para ser medidos, suelen requerir de una mayor credibilidad para ser ofertados. Productos que pueden cumplir con estos criterios son los servicios de reparacin, los cortes de cabello o el lavado de ropa.

    .row.justify-content-center.movil
      .col-lg-6
        a.anexo.mb-4(:href="obtenerLink('/downloads/Clasificacion_de_productos.pdf')" target="_blank")
          .anexo__icono
            img(src="@/assets/template/icono-pdf.svg")
          .anexo__texto
            p Anexo. Descargar clasificacion de productos.
    
    h3.titulo-tercero 2.2.2.	Fichas tcnicas

    p(data-aos="fade-left") La elaboracin de los productos suele constituirse como una actividad altamente repetitiva, en la que se siguen una serie de lineamientos que propenden por la estandarizacin del proceso. Esto se debe a que la uniformidad en las lneas de produccin permite la fabricacin en serie de bienes con un nivel de calidad muy semejante entre ellos, lo que garantiza satisfaccin en los clientes, mientras que de forma simultnea permite prever los requerimientos de insumos necesarios para mantener la operacin durante franjas de tiempo determinadas.
    p.mb-5(data-aos="fade-left") La ficha tcnica se erige como un documento identificador de la composicin de cualquier producto que ha requerido de algn tipo de proceso para su elaboracin. Usualmente incluye la descripcin tcnica y objetiva de los insumos utilizados en la produccin del bien, por lo que es una herramienta de especial utilidad en la obtencin de certificaciones de calidad.

    .row.justify-content-center
      .col-sm-8.mb-3
        .cajon.p-4(style="background-color:#FFFAD0")(data-aos="flip-up")
          p Su objetivo principal es facilitar la estandarizacin de los procesos en la empresa productora, puesto que les entrega a los operarios la informacin necesaria para la elaboracin de cada producto. Adems, facilita labores de acompaamiento de personal ajeno a la produccin como los auditores.
          p Su uso resulta bastante comn en la industria alimenticia, ya que le permite al consumidor un acercamiento a los ingredientes utilizados para la elaboracin del alimento procesado que piensa adquirir. Esto sin contar con los requerimientos normativos, que en muchas ocasiones exigen esta informacin detallada para aprobar su comercializacin.
      .col-sm-2
        figure(data-aos="flip-up")
          img(src='@/assets/curso/temas/tema2/8.svg', alt='Texto que describa la imagen')

    .row.justify-content-center
      .col-11.mt-5.mb-5.offset-1
        p(data-aos="fade-left") Si se desea disear una ficha tcnica para propsitos de calidad de produccin se recomienda que incluya por lo menos los siguientes elementos:

    AcordionA(tipo="b" clase-tarjeta="tarjeta tarjeta--gris")
      .row(titulo="Nombre del producto")
        .col-md-12.mb-4.mb-md-0
          p Debe diferenciarse de cualquier otro producto elaborado en la compaa, lo que implica hacer precisiones respecto a tamaos o presentaciones, por ejemplo, diferenciar entre versiones lquidas o slidas.
      
      div(titulo="Tiempo de preparacin")
        .col-md-12.mb-4.mb-md-0
          p Si requieren someterse a procesos de coccin o enfriamiento, se puede especificar el tiempo que deben permanecer en cada etapa. Por ejemplo, la pasteurizacin de la leche implica someterla a bajas y altas temperaturas por periodos de tiempo determinados.

      div(titulo="Mano de obra requerida")
        .col-md-12.mb-4.mb-md-0
          p Puede detallarse por cargos operativos y suele presentarse en horas de trabajo. Esto significa, el tiempo que un operario le destina para aplicar todos los procedimientos requeridos a cada unidad producida.

      div(titulo="Equipamiento usado")
        .col-md-12.mb-4.mb-md-0
          p Algunos productos demandan el uso de una maquinaria especfica. Es importante dejar esto en claro para evitar reprocesos, retrasos en las lneas de produccin o la utilizacin errnea de maquinaria que altere la disposicin del producto.

      div(titulo="Tamao de la porcin")
        .col-md-12.mb-4.mb-md-0
          p Segn la presentacin del producto, puede expresarse en litros, kilos, libras o gramos, entre otros.

      div(titulo="Imagen del producto")
        .col-md-12.mb-4.mb-md-0
          p para apoyar la identificacin del bien, pueden incluirse imgenes orientativas de la presentacin del producto, el logo o informacin relevante adicional.

      div(titulo="Instrucciones especiales")
        .col-md-12.mb-4.mb-md-0
          p Ciertos productos pueden requerir tratamientos especiales, por ejemplo, si son delicados o perecederos, o si requieren de ciertas condiciones ambientales para su conservacin. Este aspecto toma vital importancia cuando los productos abandonan la fbrica de produccin y son manipulados por otros agentes del canal de distribucin que carecen de los conocimientos tcnicos.

    separador
    .titulo-segundo.color-secundario
      h2#t_2_3 2.3 Plan de Mercadeo

    .row.justify.content-center.mb-5 
      .col-sm-8
        p El plan de mercadeo es un documento en el que se plasman los objetivos que espera alcanzar la empresa en materia comercial, as como las estrategias a implementar para hacerlo posible junto con el costo para ejecutarlas. Su utilidad radica en la correcta planeacin y destinacin de recursos con suficiente anticipacin para garantizar que los flujos de efectivo se destinen de forma correcta y para que los esfuerzos se encaminen hacia una meta clara y definida.
        p Comnmente, el plan de mercadeo se formula para ser ejecutado en periodos de un ao, donde al final del ejercicio, se har una evaluacin en la que se compara lo proyectado con lo efectivamente alcanzado. Para posibilitar esto ltimo, deben cuantificarse todos los elementos que aporten a hacer un juicio por parte de los evaluadores. Lo anterior conlleva canales de comunicacin efectivos y oportunos con el personal del rea comercial, los proveedores y los clientes, as como con todos los dems integrantes de la cadena de distribucin del producto.
      .col-sm-4
        figure(data-aos="flip-up")
          img(src='@/assets/curso/temas/tema2/9.jpg', alt='Texto que describa la imagen')

    h3.titulo-tercero 2.3.1	Elementos

    p.mb-5(data-aos="fade-left") Como se ha mencionado el plan de mercadeo es un documento, y por lo tanto su elaboracin y formato est sometido a juicio del personal estratgico que lo disee. Sin embargo, es recomendable que dentro de su estructura se incluyan por lo menos tres grandes secciones as:

    .row.justify-content-center 
      .cajon.p-4(style="background-color:#DBF9BD")(data-aos="flip-up")
        .row.justify-content-center
          .col-sm-3
            figure(data-aos="flip-up")
              img(src='@/assets/curso/temas/tema2/10.jpg', alt='Texto que describa la imagen')
          .col-sm-7
            h5  #[i Marketing] analtico
            p(data-aos="fade-left") En esta primera seccin se debe hacer un anlisis de la empresa, el mercado objetivo y la competencia. La informacin recolectada en esta etapa resulta de vital importancia para las medidas que se tomen posteriormente, puesto que las estrategias deben atacar aquellas amenazas o debilidades identificadas.
            p(data-aos="fade-left") Es recomendable que el estudio aborde los factores internos y externos de la empresa, lo que implica evaluar al personal de ventas, la reputacin del producto, los precios de los bienes sustitutos, la demografa de la poblacin, y cualquier otro factor que pueda inferir en los resultados comerciales. Es recomendable aplicar una matriz FODA que permita maniobrar con las fortalezas, oportunidades, debilidades y amenazas de forma efectiva.

    .row.justify-content-center.mt-5 
      .col-10
        p(data-aos="fade-left") La siguiente figura, aborda los cuatro componentes principales del #[i marketing] analtico y algunas de las actividades que pueden tenerse en cuenta para documentar el plan de mercadeo.

    .titulo-sexto.color-acento-contenido.offset-1
      h5 Figura 5
      span #[em Componentes del Marketing analtico]

    .row.justify-content-center
      .col-lg-10.desktop
        figure(data-aos="flip-up")
          img(src='@/assets/curso/temas/tema2/11.svg', alt='En la figura se observa anlisis interno, anlisis externo, estudio de mercado y anlisis FODA.')
          figcaption Nota. Ministerio del trabajo (2018).

    .row.justify-content-center
      .col-lg-10.movil
        figure(data-aos="flip-up")
          img(src='@/assets/curso/temas/tema2/12.svg', alt='En la figura se observa anlisis interno, anlisis externo, estudio de mercado y anlisis FODA.')
          figcaption Nota. Ministerio del trabajo (2018).

    .row.justify-content-center
      .col-10.mt-5
        h5(data-aos="fade-left")  #[i Marketing ]estratgico

    .row.justify-content-center 
      .col-sm-3
        figure(data-aos="flip-up")
          img(src='@/assets/curso/temas/tema2/13.jpg', alt='Texto que describa la imagen')
      .col-sm-7
        p(data-aos="fade-left") una vez se ha recopilado toda la informacin relevante para la gestin comercial, se procede a la postulacin de metas que puedan ser alcanzados con los recursos disponibles. A partir de ello, se plantean las estrategias que represente una mayor relacin costo  beneficio y se estructuran incluyendo los objetivos especficos y las tareas a desarrollar.
        p(data-aos="fade-left") En primer lugar, se debe estimar la demanda a partir de la informacin recolectada, por ejemplo, si se espera tener una participacin del 10 % del mercado, y en la poblacin abordada se venden 1.000 unidades de productos similares, la meta de ventas debe ser de 100 unidades en el periodo de tiempo definido. A partir de ello se define la fuerza de ventas que ser necesaria, y la implementacin de medidas adicionales asociadas a cobertura, publicidad, distribucin entre otros.
  
    .row.justify-content-center
      .tarjeta.p-4(style="background-color:#3B5620")(data-aos="flip-up")
        .row.justify-content-center.align-items-center
          .col-sm-1.mb-3
            figure(data-aos="flip-up")
              img(src='@/assets/curso/temas/tema2/14.svg', alt='Texto que describa la imagen')
          .col-sm-9
            p.text-white Muchas interpretaciones pueden derivarse del anlisis anterior, definir el mercado actual y el mercado meta traza un camino a seguir y un objetivo claro, pero las estrategias deben ser lo suficientemente precisas para contemplar aspectos como la segmentacin del mercado, las variaciones en los precios, y las ventajas competitivas con las que se espera contar para favorecer la eleccin del producto propio sobre el producto de la competencia.


    .row.justify-content-center
      .col-10.mt-5
        h5(data-aos="fade-left")  #[i Marketing ]operativo
        p(data-aos="fade-left") En esta fase se materializan las estrategias propuestas, puesto que se finaliza el diseo, y se implementan las tareas definidas. Previo y durante la etapa de ejecucin se debe llevar un control de las expensas necesarias para actualizar el control presupuestal y tener claridad sobre la disponibilidad del efectivo de manera permanente.
        p(data-aos="fade-left") El #[i marketing] operativo comprende 4 pasos en los que se hace uso de toda la informacin recolectada en el #[i marketing] analtico y estratgico:

    .row.justify-content-center 
      .col-10
        <hr style="border:15px;">
    .row.justify-content-center 
      .col-sm-1
        figure(data-aos="flip-up")
          img(src='@/assets/curso/temas/tema2/15.svg', alt='Texto que describa la imagen')
      .col-sm-9
        p #[b  4p:] la fase de las 4 P defina la forma en la que el producto llegar a los clientes; su nombre proviene del anlisis de los factores ms relevantes en este sentido, que son: el producto, el precio, la plaza y la promocin. Lo anterior implica que debe efectuarse un estudio que contemple las caractersticas del producto, su precio de venta en relacin con los terceros, la plaza, entendida como el lugar donde se ofertan los bienes, y la promocin, que refiera a los esfuerzos comerciales para penetrar en el mercado.
      .col-10
        <hr style="border:15px;">

    .row.justify-content-center 
      .col-sm-1
        figure(data-aos="flip-up")
          img(src='@/assets/curso/temas/tema2/16.svg', alt='Texto que describa la imagen')
      .col-sm-9
        p #[b Comunicacin:] aborda todos los canales para la transmisin de informacin, esto incluye la comunicacin asertiva entre los funcionarios de las diversas reas que colaboran para la elaboracin, distribucin y venta de bienes, as como la debida retroalimentacin obtenida por parte de los consumidores. Es recomendable apoyarse en las tecnologas de la informacin para facilitar el proceso, haciendo uso de herramientas contempladas en el #[i marketing] digital como la gestin redes sociales o mensajera instantnea.

      .col-10
        <hr style="border:15px;">
        

    .row.justify-content-center 
      .col-sm-1
        figure(data-aos="flip-up")
          img(src='@/assets/curso/temas/tema2/17.svg', alt='Texto que describa la imagen')
      .col-sm-9
        p #[b Puesta en marcha:] para este paso se deben definir las tareas y los responsables de estas. La asignacin puede dirigirse a reas, cargos o personas segn las necesidades; sin embargo, es importante recalcar que el factor tiempo resulta determinante, puesto que ciertas estrategias requieren de una secuencia para ser efectivas. Por ejemplo, una estrategia publicitaria puede tener diferentes efectos si la campaa en medios se realiza antes (para crear expectativa) o despus del lanzamiento de un producto. 

      .col-10
        <hr style="border:15px;">
    

    .row.justify-content-center 
      .col-sm-1
        figure(data-aos="flip-up")
          img(src='@/assets/curso/temas/tema2/18.svg', alt='Texto que describa la imagen')
      .col-sm-9
        p #[b Presupuesto:] este tipo de planeacin no puede obviar el factor econmico que implica su implementacin. La empresa como rgano social debe trabajar de manera cooperativa con las dems reas organizacionales, puesto que la armona garantiza que los propsitos comunes sean alcanzados.
        p Una campaa muy costosa puede traer una demanda tan grande que la capacidad productiva no logre abastecerla, de la misma manera, la aplicacin de una estrategia publicitaria puede representar ms dinero del que tiene a disposicin la empresa, lo que implica posibles sobreendeudamientos o incumplimientos en los pagos que afecten la reputacin, causando un efecto contrario al buscado.
        p El presupuesto debe elaborarse de manera previa y simultnea a la programacin de las tareas propias de la puesta en marcha, de tal forma que se tenga certeza de la disponibilidad de recursos en las fechas esperadas. Este trabajo suele requerir del acompaamiento del rea financiera, quienes debern dar aprobacin previa implementacin, para garantizar que todas las tareas puedan llevarse a cabo de acuerdo con el programa.

      .col-10
        <hr style="border:15px;">        

    h3.titulo-tercero 2.3.2	Importancia en el ciclo productivo

    p.mb-5(data-aos="fade-left") Generalmente, la demanda de los productos obedece a muchos factores sobre los cuales la empresa no siempre tiene control; condiciones econmicas, polticas pblicas y la competencia, son solo algunos de ellos. Debido a esto, lo ms habitual a la hora de disear un ciclo productivo, es que el plan de mercadeo haga de punto de partida en el ciclo productivo y posteriormente se debe tener en cuenta lo siguiente:

    figure
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/RbnVKRrsk8U?si=DdNDKPk4knDUhtKr" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)

    separador
    .titulo-segundo.color-secundario
      h2#t_2_4 2.4 Elementos del costo

    .row.justify-content-center
      .col-sm-3 
        figure(data-aos="flip-up")
          img(src='@/assets/curso/temas/tema2/19.jpg', alt='Texto que describa la imagen')
      .col-sm-8
        p(data-aos="fade-left") Est compuesta por todos los materiales utilizados en la elaboracin del producto. Son insumos tangibles y susceptibles a transformacin durante el ciclo de produccin; pueden ser clasificados como directos o indirectos segn la relevancia y relacin que tengan con el producto terminado.
        p(data-aos="fade-left") La materia prima directa se clasifica como tal si da cumplimiento a los dos siguientes conceptos: Materialidad e importancia relativa. El primero se asocia al costo de la inversin o su significancia en la elaboracin, es decir, qu tanto valor econmico aporta al costo total. Como ejemplo de lo anterior, se considera materia prima directa la tela en la confeccin de los pantalones, donde resulta evidente que no solo representa uno de los insumos que mayor valor econmico atribuyen a la produccin, sino tambin aquel que tiene mayor participacin en la composicin del producto terminado.

    .row.justify-content-center.mt-5
      .tarjeta.p-4(style="background-color:#3B5620")(data-aos="flip-up")
        .row.justify-content-center.align-items-center
          .col-sm-1.mb-3
            figure(data-aos="flip-up")
              img(src='@/assets/curso/temas/tema2/20.svg', alt='Texto que describa la imagen')
          .col-sm-9
            p.text-white La importancia relativa se relaciona con lo indispensable de un material en el proceso de produccin. As, continuando con el ejemplo anterior, los botones del pantaln tambin pueden ser considerados materia prima directa, pues si bien, no son tan costosos, son necesarios para dar por terminada su produccin.

    .row.justify-content-center
      .col-sm-10
        .bloque-texto-b.color-secundario.p-4.mt-5
          .bloque-texto-b__texto
            i.fas.fa-quote-left
            h5.mb-0 En palabras de Guarnizo y Crdenas (2015) la materia prima no solo se compone por el valor de compra de los insumos, sino tambin, por todas aquellas erogaciones que se asocian a su adquisicin y disposicin. Por ejemplo, los costos de embalaje, el bodegaje, las prdidas, son algunos elementos que encajaran dentro de la definicin de materia prima. 
            i.fas.fa-quote-right

    p(data-aos="fade-left") En la siguiente figura se presentan a modo orientativo algunos de los elementos que constituyen el costo de la materia prima, separadas en funcin de la fase en la que esta se encuentra dentro de la produccin:

    .titulo-sexto.color-acento-contenido.offset-1
      h5 Figura 6
      span #[em Costos asociados a la materia prima]

    .row.justify-content-center
      .col-lg-10.desktop
        figure(data-aos="flip-up")
          img(src='@/assets/curso/temas/tema2/21.svg', alt='En la figura se observa costos como compras, mantenimiento, administracin y faltantes.')
          figcaption Nota. Adaptado de Guarnizo y Crdenas (2015).

    .row.justify-content-center
      .col-lg-10.movil
        figure(data-aos="flip-up")
          img(src='@/assets/curso/temas/tema2/22.svg', alt='En la figura se observa costos como compras, mantenimiento, administracin y faltantes.')
          Figcaption Nota. Adaptado de Guarnizo y Crdenas (2015).

    .row.justify-content-center.mt-5 
      .col-sm-10
        h4 Mano de obra

    .row.justify-content-center.align-items-center 
      .col-sm-3
        figure(data-aos="flip-up")
          img(src='@/assets/curso/temas/tema2/23.jpg', alt='Texto que describa la imagen')
      .col-sm-7
        p La mano de obra se constituye como el segundo elemento de los costos de produccin, al cual se asigna el valor del trabajo realizado por las personas que se involucran con la produccin y transformacin de los insumos. Al igual que la materia prima, puede clasificarse como directa o indirecta.
        p En principio, aquellos funcionarios cuya labor es trabajar transformando los insumos, son considerados mano de obra directa, por ejemplo, el operario encargado de ordear las vacas en una granja pecuaria. Por otro lado, la mano de obra indirecta incluye la labor desempeada por trabajadores de apoyo o direccin del rea, pero sin participar directamente sobre la produccin. Ejemplo de esto son los vigilantes o supervisores de las reas donde se encuentra el rea de produccin.

    .row.justify-content-center.mt-5
      .tarjeta.p-4(style="background-color:#3B5620")(data-aos="flip-up")
        .row.justify-content-center.align-items-center
          .col-sm-1.mb-3
            figure(data-aos="flip-up")
              img(src='@/assets/curso/temas/tema2/24.svg', alt='Texto que describa la imagen')
          .col-sm-9
            p.text-white Respecto a los elementos que componen el costo de la mano de obra, se deben tener presentes todas las remuneraciones asociadas a la relacin con el trabajador, esto incluye las prestaciones sociales, la seguridad social, las bonificaciones y cualquier otra erogacin que se d como retribucin al trabajo prestado. Otros costos que podran incluirse si la administracin as lo considera son gastos accesorios como los de reclutamiento, auxilios, capacitaciones, entre otros.

    .row.justify-content-center.mt-5.mb-3 
      .col-sm-10
        h4 Costos indirectos de fabricacin

    .row.justify-content-center.align-items-center 
      .col-sm-3
        figure(data-aos="flip-up")
          img(src='@/assets/curso/temas/tema2/25.jpg', alt='Texto que describa la imagen')
      .col-sm-7
        p Agrupan todas las erogaciones indirectas a la produccin, incluye la materia prima y mano de obra que se catalogaron previamente como indirectas, y adicionan otros conceptos que no pueden atribuirse o destinarse de forma especfica a cada unidad producida.
        p Esta categora absorbe todo valor residual que no cumpla con la clasificacin de insumo directo, por lo que, de su precisin en el clculo, depende la correcta estimacin de los costos de produccin. Ejemplo de otros rubros que encajan con esta clasificacin son los mantenimientos hechos a las maquinarias, el combustible que estas requieren para producir o el arrendamiento del inmueble donde se ejecuta la produccin.

    separador
    .titulo-segundo.color-secundario
      h2#t_2_5 2.5 Clasificacin

    p(data-aos="fade-left") Una vez se tiene claridad del concepto de costo de produccin y los elementos que lo integran, se pueden clasificar segn diversos criterios como los siguientes:

    .row.justify-content-center 
      .col-sm-8
        ImagenInfograficaB.color-secundario.mb-5
          template(v-slot:imagen)
            figure(data-aos="flip-up")
              img(src='@/assets/curso/temas/tema2/26.svg', alt='Texto que describa la imagen')
              
          div(x="49.6%" y="93.3%" tooltip="Segn su uso en planeacin" numero="") 
            h5 Segn su uso en planeacin
            p En primer lugar, estn los costos estimados, que surgen de metodologas de anlisis y observacin. No son exactos y su margen de error est condicionado al nivel de detalle en la que se haya efectuado los procedimientos de clculos. Son aplicables a los insumos que no son materiales en el proceso de elaboracin. En segundo lugar, se ubican los costos estndar, utilizados en empresas con volmenes de produccin significativos y bajo un sistema altamente repetitivo. Analizan la informacin histrica disponible, y anticipan el costo de produccin a partir de ello, su margen de error suele ser muy bajo dado que trabajan con variables ya conocidas.

          div(x="18%" y="19.5%" tooltip="Segn su relacin con la produccin" numero="") 
            h5 Segn su relacin con la produccin
            p Bajo este modelo, los costos se discriminan en variables, que son aquellos que tienen una relacin directa con las unidades producidas, por ejemplo, a mayor cantidad de camisas a producir, mayor la cantidad de tela a utilizar. Como contraparte estn los costos fijos, que no se ven afectados por la produccin, aqu podemos encontrar rubros como el salario de las aseadoras de planta de produccin.

          div(x="81.7%" y="19.5%" tooltip="Segn su relacin con el producto" numero="") 
            h5 Segn su relacin con el producto
            p La regla aplicable a este sistema nos lleva a determinar si existe o no una relacin directa entre el insumo y la generacin del producto terminado. Se consideran directos los costos asignables a un producto en especfico, como lo sera la materia prima objeto de transformacin, o el salario pagado al operario que trabaja en una lnea de produccin en concreto. Los costos indirectos, por su parte, corresponden a aquellos que, aunque tienen relacin con la produccin, son comunes a varios tipos de productos o cuesta asignarlos de forma directa a uno en especfico.

    separador
    .titulo-segundo.color-secundario
      h2#t_2_6 2.6 Clculo

    p.mb-5(data-aos="fade-left") Si bien previamente se present una conceptualizacin de los elementos del costo de produccin, determinar su valor es un proceso que demanda la aplicacin de diversas metodologas para cada uno de ellos. A continuacin, se presentan los sistemas de ms fcil accesibilidad para determinar el valor de cada uno de los elementos del costo.

    .row.justify-content-center.mb-3 
      .col-sm-10
        h3.titulo-tercero 2.6.1	Materia Prima

    .row.justify-content-center.align-items-center 
      .col-sm-1
        figure(data-aos="flip-up")
          img(src='@/assets/curso/temas/tema2/27.svg', alt='Texto que describa la imagen')
      .col-sm-11
        p En este rubro se incluyen todos los insumos materiales para ser utilizados en la produccin. En el mbito financiero, este tipo de elementos se clasifican como inventarios, y los sistemas utilizados para su valoracin se encuentran regulados en la normativa colombiana. En la actualidad, se puede hacer uso de los dos siguientes sistemas:

    .row.justify-content-center 
      .col-10
        h4  Sistema de inventarios peridico: 
        p(data-aos="fade-lefet") Bajo este modelo, el costo de la materia prima se determina a una fecha de corte, a travs del conteo fsico de las unidades que no han sido utilizadas o vendida, es decir, el inventario final. La frmula a utilizar es conocida como el juego de inventarios y se presenta as: 

    .row.justify-content-center.mt-5
      .col-6
        .tarjeta.p-4(style="background-color:#3B5620")(data-aos="flip-up")
          .row.justify-content-center.align-items-center
            .col-sm-3.mb-3
              figure(data-aos="flip-up")
                img(src='@/assets/curso/temas/tema2/28.svg', alt='Texto que describa la imagen')
            .col-sm-7
              h4.text-white Inventario inicial 
              p.text-white (+) Compras brutas
              p.text-white () Devoluciones en compras. 
              p.text-white = Mercancas disponibles para la venta 
              p.text-white () Inventario final. 
              p.text-white = Costo de venta.

    .row.justify-content-center.mt-5 
      .col-10
        p(data-aos="fade-lefet") Es un sistema de fcil y econmica aplicacin, puesto que el esfuerzo recae sobre un trabajo manual y fsico, actualmente ha ido perdiendo relevancia, pues las facilidades tecnolgicas posibilitan usar herramientas ms precisas. El principal problema de este modelo, es que para estimar el costo es necesario realizar todo el proceso de verificacin de existencias, implicando que no se puede tener datos exactos inmediatos y permanentes.

    .row.justify-content-center 
      .col-10
        h4  Ejemplo de sistema de inventarios peridicos.
        p(data-aos="fade-lefet") Por ejemplo, si una empresa hace compras por $ 100 y a final del ao le queda un saldo de materiales por valor de $ 20, se deduce que el costo de los insumos fue de $ 80, bien sea porque hayan sido utilizados en la produccin, o se hayan deteriorado o perdido. Para calcular este costo fue necesario determinar el valor del inventario final, situacin que demanda siempre un conteo fsico, y por lo tanto, hace muy exigente la estimacin del costo si se quiere conocer de manera frecuente.

    .row.justify-content-center 
      .col-10
        h4  Sistema de inventarios permanentes
        p.mb-5(data-aos="fade-lefet") Ofrece una medicin ms precisa de las materias primas, dispone con claridad de la cifra en cualquier momento, y la informacin es mucho mayor a la de su contraparte. Las metodologas utilizadas son:

    .row.justify-content-center 
      .col-10
        AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--gris")(data-aos="flip-up")
          .row.justify-content-center.align-items-center(titulo="Primeras en entrar, primeras en salir (PEPS)")
            .col-sm-2.mb-3
              figure
                img(src='@/assets/curso/temas/tema2/29.svg', alt='Texto que describa la imagen')
            .col-md-9.mb-4.mb-md-0
              p Las unidades utilizadas en la produccin se disponen en el mismo orden de ingreso al inventario. Los saldos de materia prima siempre correspondern a las ltimas unidades compradas.

          div(titulo="Etapa de sntesis o produccin")
            .row.justify-content-center.align-items-center(titulo="ltimas en entrar, primeras en salir (UEPS)")
              .col-sm-2.mb-3
                figure
                  img(src='@/assets/curso/temas/tema2/30.svg', alt='Texto que describa la imagen')
              .col-md-9.mb-4.mb-md-0
                p Su mecnica es opuesta al anterior, puesto que la materia prima a utilizar ser la ltima comprada, esto lleva a que los saldos del inventario final se compongan por la materia prima adquirida con mayor antigedad, esto puede generar desfases en los precios y llevar a un sesgo en la interpretacin de la informacin.

          div(titulo="Etapa de acondicionamiento o procesamiento")
            .row.justify-content-center.align-items-center(titulo="Promedio ponderado")
              .col-sm-2.mb-3
                figure
                  img(src='@/assets/curso/temas/tema2/31.svg', alt='Texto que describa la imagen')
              .col-md-9.mb-4.mb-md-0
                p El sistema funciona afectando el costo de la materia prima en funcin de todas las compras realizadas en el tiempo. Esto significa que las variaciones de los precios ajustan el valor promedio de todas las existencias disponibles en el inventario.

    .row.justify-content-center 
      .col-10
        p(data-aos="fade-left").mb-5 En Colombia, la legislacin tributaria solo permite el uso de los sistemas PEPS y promedio ponderado, dado que son los que gozan de mayor precisin a la hora de determinar el costo de los inventarios. Cualquier de los dos, hace uso de una herramienta conocida como Krdex, que consta de una hoja de trabajo en la que se computan las compras efectuadas en orden cronolgico; de esta misma forma, se reconocen las salidas cuando se utilizan en el proceso de produccin. 

    .row.justify-content-center
      .col-10
        .tarjeta.p-3.mb-5(style="background-color:#FFFAD0")(data-aos="flip-up") 
          .row.justify-content-around.align-items-center
            .col-3.col-sm-2.col-lg-1
              img(src="@/assets/curso/temas/tema2/32.svg")
            .col
              .row.justify-content-between.align-items-center
                .col.mb-3.mb-sm-0
                  p.text-small En el Anexo 1_ Hoja de krdex, podr consultar ejemplos relacionados con este tipo de herramienta, en donde se ofrece de mayor precisin al respecto.
                .col-sm-auto
                  a.boton.color-acento-contenido.texto-blanco(:href="obtenerLink('/downloads/Anexo1_Hoja_de_kardex.pdf')" target="_blank")
                    span Descargar
                    i.fas.fa-file-download

    .titulo-sexto.color-acento-contenido.offset-1
      h5 Tabla 3 
      span #[em Comparativa sistemas de valuacin de inventario]

    .row.justify-content-center 
      .col-10
        .tabla-a.mb-5 
          table
            thead.text-white(style="background-color:#3B5620")(data-aos="flip-up") 
