    .row.justify-content-center.mb-3 
      .col-sm-10
        h3.titulo-tercero 2.6.2	Mano de obra
    p(data-aos="fade-left") El costo de la mano de obra en Colombia involucra las prestaciones y beneficios a los que los empleados tienen derecho. Un contrato de trabajo comprende un salario bsico, unas prestaciones sociales, unos aportes parafiscales y de seguridad social, as como cualquier otra retribucin asociada a las labores ejecutadas en funcin de su trabajo.
    p(data-aos="fade-left") La normativa colombiana es muy extensa respecto a las condiciones de la contratacin laboral, el cdigo sustantivo del trabajo de 1950 presenta el compilatorio ms importante en el pas en este aspecto, de su interpretacin se sustraen los siguientes apartados, que resultan de especial importancia para estimar el costo de la mano de obra:

    .row.justify-content-center 
      .col-10
        .row.mb-5
          .col-sm-6.col-lg-3.mb-4.mb-lg-0
            .tarjeta--boton.color.p-4(style="background-color:#DBF9BD; height:100%")
              .row.justify-content-center.mb-3
                .col-7
                  figure
                    img(src='@/assets/curso/temas/tema2/33.svg', alt='Texto que describa la imagen')

              h3.text-center Salario
              p.text-small Constituye salario no slo la remuneracin ordinaria, fija o variable, sino todo lo que recibe el trabajador en dinero o en especie como contraprestacin directa del servicio, sea cualquiera la forma o denominacin que se adopte, como primas, sobresueldos, bonificaciones habituales, valor del trabajo suplementario o de las horas extras, valor del trabajo en das de descanso obligatorio, porcentajes sobre ventas y comisiones (Cdigo Sustantivo del Trabajo, 1950, Artculo 127).

          .col-sm-6.col-lg-3.mb-4.mb-lg-0
            .tarjeta--boton.color.p-4(style="background-color:#DBF9BD; height:100%")
              .row.justify-content-center.mb-3
                .col-7
                  figure
                    img(src='@/assets/curso/temas/tema2/34.svg', alt='Texto que describa la imagen')

              h3.text-center Horas extra
              p.text-small Trabajo suplementario o de horas extras es el que excede de la jornada ordinaria, y en todo caso el que excede de la mxima legal (Cdigo Sustantivo del Trabajo, 1950, Artculo 159).

          .col-sm-6.col-lg-3.mb-4.mb-lg-0
            .tarjeta--boton.color.p-4(style="background-color:#DBF9BD; height:100%")
              .row.justify-content-center.mb-3
                .col-7
                  figure
                    img(src='@/assets/curso/temas/tema2/35.svg', alt='Texto que describa la imagen')

              h3.text-center Vacaciones
              p.text-small Los trabajadores que hubieren prestado sus servicios durante un ao tienen derecho a quince (15) das hbiles consecutivos de vacaciones remuneradas (Cdigo Sustantivo del Trabajo, 1950, Artculo 186).

          .col-sm-6.col-lg-3.mb-4.mb-lg-0
            .tarjeta--boton.color.p-4(style="background-color:#DBF9BD; height:100%")
              .row.justify-content-center.mb-3
                .col-7
                  figure
                    img(src='@/assets/curso/temas/tema2/36.svg', alt='Texto que describa la imagen')

              h3.text-center Cesantas
              p.text-small Todo empleador est obligado a pagar a sus trabajadores, y a las dems personas que se indican en este Captulo, al terminar el contrato de trabajo, como auxilio de cesanta, un mes de salario por cada ao de servicios y proporcionalmente por fraccin de ao (Cdigo Sustantivo del Trabajo, 1950, Artculo 249).

    .row.justify-content-center 
      .col-10
        .row.mb-5.justify-content-center
          .col-sm-6.col-lg-3.mb-4.mb-lg-0
            .tarjeta--boton.color.p-4(style="background-color:#DBF9BD; height:100%")
              .row.justify-content-center.mb-3
                .col-7
                  figure
                    img(src='@/assets/curso/temas/tema2/37.svg', alt='Texto que describa la imagen')

              h3.text-center Prima de servicios
              p.text-small El empleador est obligado a pagar a su empleado o empleados, la prestacin social denominada prima de servicios que corresponder a 30 das de salario por ao, el cual se reconocer en dos pagos, as: la mitad mximo el 30 de junio y la otra mitad a ms tardar los primeros veinte das de diciembre. Su reconocimiento se har por todo el semestre trabajado o proporcionalmente al tiempo trabajado (Cdigo Sustantivo del Trabajo, 1950, Artculo 306).

          .col-sm-6.col-lg-3.mb-4.mb-lg-0
            .tarjeta--boton.color.p-4(style="background-color:#DBF9BD; height:100%")
              .row.justify-content-center.mb-3
                .col-7
                  figure
