    p.mb-5(data-aos="fade-left") La distribucin y asignacin de los costos indirectos de fabricacin aporta ms complejidad al clculo del costo de produccin de lo que se podra pensar, lo anterior debido a que existen limitaciones significativas para determinar la porcin de un costo indirecto a una unidad producida. Por ejemplo, si se debe reparar una mquina de ordeo, en cunto se debe incrementar el costo de produccin de cada litro de leche.

    .row.justify-content-center 
      .col-10
        .row.justify-content-center 
          .col-md-8
            p(style="color:#3B5620") Este tipo de situaciones implica que la asignacin de los costos indirectos obedezca a la cantidad de unidades producidas en un periodo concreto. Existen diversos elementos de juicio, pero suelen converger en una apreciacin, la asignacin del costo estar supeditada a los niveles de produccin de la planta.
            p Si se producen varios productos en diversas lneas de produccin, por ejemplo, en una granja pecuaria donde se produce leche y al mismo tiempo se vende la carne del ganado, la situacin puede resultar ms complejo. Retomando el ejemplo anterior, el dao de una mquina de ordeo debe asignarse a la lnea productiva de la leche, pero qu sucede entonces en casos como el salario del administrador, donde su trabajo es indistinto entre las dos lneas de produccin.
