    .titulo-segundo.color-secundario
      h2#t_2_7 2.7 rdenes de produccin

    p(data-aos="fade-left") Las rdenes de produccin, tambin conocidas como rdenes de pedido, suelen ser utilizadas en empresas en las que la lnea de produccin no opera de manera permanente. Bajo este sistema, la determinacin del coste de un lote de producto terminado se estima de forma independiente a otros, por lo que pueden existir variaciones en el costo de venta unitario entre una y otra orden.
    p(data-aos="fade-left") Este sistema ofrece una gran utilidad en unidades productivas que trabajan por encargo, puesto que las mediciones varan en funcin de la situacin actual. El siguiente formato propone un esquema para utilizar bajo esta metodologa; su diligenciamiento suele ser precedido por una solicitud comercial, es decir, el rea de ventas indica la unidad de productos vendidos, y de cara a esto, el departamento de produccin determina el costo unitario de cara a las exigencias presentadas, ver tabla.

    .titulo-sexto.color-acento-contenido.offset-2
