          img(src='@/assets/curso/temas/tema2/44.svg', alt='En la tabla se observa el formato para orden de produccin, que incluye materiales, mano de obra directa, costos indirectos, en cantidad y costo.')
          figcaption Nota. Guarnizo Cuellar y Crdenas (2015).

    h3.titulo-tercero.mt-5 2.7.1	Caractersticas

    .row
      p(data-aos="fade-left") Las ordenes de produccin preceden el inicio del ciclo productivo, por lo que su emisin resulta un requisito previo. En ellas se consigna informacin relevante como las unidades a producir, su identificacin y el nmero consecutivo de la orden con el fin de establecer prioridades. Las siguientes son algunas de las caractersticas ms importantes de estas:

    .row.justify-content-center
      .col-md-10
        .row.justify-content-center.align-items-center 
          .col-md-4
            figure
              img(src='@/assets/curso/temas/tema2/45.jpg', alt='Texto que describa la imagen')
          .col-md-8 
            .cajon.p-4.mb-2(style="background-color:#DBF9BD")(data-aos="flip-up") 
              p.text-small Se espera estimar un costo de produccin distinto para cada pedido, por lo que es comn ver la aplicacin de este tipo de metodologas en unidades productivas que no mantienen una lnea de produccin estandarizada o permanente.
            .cajon.p-4.mb-2(style="background-color:#DBF9BD")(data-aos="flip-up") 
              p.text-small Se hace necesaria una planificacin previo inicio de la produccin, puesto que el alistamiento del material y el diligenciamiento de la orden de produccin vara de acuerdo con las unidades que se esperan producir.
            .cajon.p-4.mb-2(style="background-color:#DBF9BD")(data-aos="flip-up") 
              p.text-small La produccin requiere de un pedido previo, lo que implica, que si no se efectan ventas, los niveles de produccin se reducirn al mnimo. El trabajo por encargo suele optimizar en costos de bodegaje o almacenamiento, pero los costos se pueden ver castigados por la capacidad ociosa de la planta.

    h3.titulo-tercero.mt-5 2.7.2.	Diligenciamiento
    p(data-aos="fade-left") Al diligenciar la orden de produccin, se espera que los insumos estimados sean los suficientes para dar cumplimiento con la demanda del cliente. Gracias a esto, se minimizan los costos de almacenaje, caducidad u obsolescencia. Una vez definidos los insumos, se les asigna un valor para calcular el costo total de la orden de produccin, y de forma derivada, obtener el costo unitario.
    p(data-aos="fade-left") Para ilustrar el diligenciamiento de una orden de produccin, se presenta la siguiente situacin: se solicita por medio de la orden de produccin 53, 4.000 litros de leche en una granja pecuaria para cumplir con los pedidos semanales producto de las ventas efectuadas por el rea comercial. Para efectos prcticos, se cuenta con un solo operario y las ordenes son de periodicidad semanal. La siguiente es la discriminacin de los costos:

    .row.justify-content-center 
      .col-10
        .row.justify-content-center 
          h5.mb-3  Materia prima
          .tarjeta.p-4(style="background-color:#FFFAD0")(data-aos="flip-up") 
            .row.justify-content-center 
              .col-md-2
                figure
                  img(src='@/assets/curso/temas/tema2/46.svg', alt='Texto que describa la imagen')
              .col-md-8
                p(data-aos="fade-left") Se estima que las vacas consumen 3 kilogramos de concentrado diario y producen 10 litros de leche en ese mismo periodo de tiempo. La compra se efecta de forma mensual a un proveedor que la ofrece a un valor de $1.500 por kilogramo, incluida la entrega en la granja. Si cada vaca puede producir 70 litros semanales, se requerir de la produccin de 58 animales para cubrir este pedido, y por lo tanto, de 1.218 kg de concentrado.

        p.mb-3.mt-3(data-aos="fade-left") Adems del pastoreo y el concentrado, las vacas requieren de una suplementacin en sales minerales con alto contenido nutricional. As, la unidad productiva suministra 1 kilogramo de sales minerales semanales a cada vaca productora. El proveedor ofrece este producto a un valor de $ 2.000 por cada kilogramo.

        .row.justify-content-center 
          h5.mb-3  Mano de obra
          p(data-aos="fade-left") La granja cuenta con un solo operario cuyo salario bsico es de $ 1.000.000, adicionalmente cuenta con las prestaciones de ley equivalentes a un 30 % del ingreso bsico, tambin se debe cancelar la seguridad social a las entidades correspondientes por un valor equivalente al 20 % del salario. Dado que la orden de produccin se lleva a cabo en una semana, el costo del empleado debe tomarse de manera proporcional (una cuarta parte del salario).

        .row.justify-content-center 
          h5.mb-3  Costos indirectos
          p(data-aos="fade-left") Los costos indirectos mensuales de la granja son los siguientes: por concepto de aseo y desinfeccin de la granja: $ 1.500.000, el pago de servicios pblicos (agua y luz) es en promedio de $ 400.000 y por ltimo, el costo de la medicacin y tratamiento veterinario asciende a $ 800.000. Al igual que con la mano de obra, se debe practicar la proporcin para una sola semana de produccin, ver tabla.

    .titulo-sexto.color-acento-contenido.offset-2
      h5 Tabla 12
      span #[em Ejemplo de orden de produccin]

    .row.justify-content-center 
      .col-8
        figure
          img(src='@/assets/curso/temas/tema2/47.svg', alt='En la tabla se observa una orden de produccin incluyendo materiales, mano de obra directa, costos indirectos.')

    p.mb-5.mt-5(data-aos="fade-left") Del formato anterior se desprende que lo que se busca con la orden de produccin es determinar las cantidades y el costo de los insumos necesarios para producir lo solicitado. Con esta informacin, se puede estimar un costo unitario y a partir de ello, establecer un margen de rentabilidad por cada litro de leche vendida.

    .row.justify-content-center
      .col-lg-10
        .cajon.color-primario.p-4.mb-4
          p El costo unitario es el resultado del costo total de la orden de produccin dividido en el nmero de unidades producidas, con los datos aqu presentados es posible determinar el valor de la utilidad bruta de la granja, puesto que si se supone un precio de venta de $ 1.000 por litro de leche, de forma rpida puede identificarse el beneficio unitario (1.000  748= $ 252) o global (4.000.000  2.993.000 = $ 1.007.000).
          
              
              
            
                


